@import '../../styles/Vars';


.top-part {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;

    @media screen and (max-width: 900px) {
        flex-direction: column;
        margin-top: 20px;
    }
}

.main-video {
    background-color: #1a1a1a;
    padding: 50px 40px;
}

.video-text {
    text-align: center;
    margin-bottom: 50px;
}

.video-title {
    margin-top: 0;
    color: #eeeeee;
}

.video-description {
    color: #888888;
    font-size: 20px;
}

.video-img {
    height: 600px;
    display: flex;
    margin: auto;
}

.video-part {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
}

.button {
    display: flex;
    justify-content: center;
    margin: 60px auto 10px auto;
    padding: 15px 25px;
    border-radius: 40px;
    width: fit-content;
    background-color: white;
    color: black;
    font-weight: 600;
    transition: all .2s ease-in-out;

    &:hover {
        background-color: rgb(212, 212, 212);
    }
}

.title-part {
    color: $primary-color;

    h1 {
        font-size: 36px;
        font-weight: 500;
        margin-top: 0px;
        margin-bottom: 30px;

        @media screen and (max-width: 900px) {
            font-size: 18px;
            margin-bottom: 10px;
            font-weight: bold;
        }
    }

    span {
        font-size: 18px;

        @media screen and (max-width: 900px) {
            font-size: 14px;
        }
    }
}

.profile-part {
    display: flex;
    gap: 20px;
    align-items: center;
    margin-bottom: 20px;

    @media screen and (max-width: 900px) {
        margin-bottom: 0px;
        gap: 10px;
    }
}

.profile-pic {
    border-radius: 100px;
    height: 65px;

    @media screen and (max-width: 900px) {
        height: 45px;
    }
}

.profile-name {
    color: $primary-color;
    font-size: 18px;
    text-align: right;
    width: min-content;

    @media screen and (max-width: 900px) {
        font-size: 14px;
    }
}

@media screen and (max-width: 900px) {
    .profile {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        margin-top: 20px;
    }
}

.accordion-50 {
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: 900px) {
        display: unset;
    }

    .accordion {
        width: 46%;

        @media screen and (max-width: 900px) {
            width: 100%;
        }
    }

    .accordion-content {
        padding: 30px 20px;
    }

}